@mixin margin-start($value) {
    -webkit-margin-start: $value;
    -moz-margin-start: $value;
    margin-inline-start: $value;
} 
.section-single-banner { 
    h3 {
        font-size: 36px;
        color: #ffffff;
    }
    .subtext {
        font-size: 24px;
        color: #ffffff; 
    }
    .btn {
        width: 100%;
        margin-top: 20px;
        padding: 12px 15px; 
        display: inline-block;
        @media (min-width: 576px) {
            width: auto;
            padding-left: 30px;
            padding-right: 30px;
            margin-top: 27px;
            min-width: 325px;
        }
    }
    .sing-banner_content {
        width: calc(100% - 30px);
        &.text-center .btn {
            margin-left: auto;
            margin-right: auto;
        }
        &.text-right .btn { 
            @include margin-start(auto);
        }
    }
}